<template>
    <div class="myLocation">
       <van-nav-bar
            title="我的地址"
            left-arrow
            @click-left="goBack"
            :border="false"
            />


        <van-address-list
            v-model="chosenAddressId"
            :list="list"
            default-tag-text="默认"
            @add="onAdd"
            @edit="onEdit"
            />

       
       
    </div>
</template>

<script>
    export default {
        name:'Location',
        data() {
            return {
                isPath          : true,
                chosenAddressId : 0,
                list            : [],
                Length          : 0,
                Aid             : [],
            }
        },
        
      
        created() {
             if(this.useId){
                //  先判断有没有地址
                 this.axios({
                    url:'/findAddress',
                    method:'GET',
                    params:{
                        appkey      : this.appkey,
                        tokenString : this.useId, 
                    }
                })
                .then(res=>{
                    if(res.data.code == 20000){
                         if(res.data.result.length == 0){
                             this.isPath = true;
                             this.$router.push({name:'Addlocat',params:{'isPath':this.isPath}})
                         }else{
                             this.isPath =  false;
                             let a = {
                                        id        : '1',
                                        name      : '张三',
                                        tel       : '13000000000',
                                        address   : '浙江省杭州市西湖区文三路 138 号东方通信大厦 7 楼 501 室',
                                        isDefault :  true,
                             }
                             res.data.result.forEach((Item,index)=>{
                                   this.list.unshift({
                                        id        : index,
                                        name      : Item.name,
                                        tel       : Item.tel,
                                        address   : `${Item.province}${Item.city}${Item.county} (${Item.addressDetail})`,
                                        isDefault : Item.isDefault == 1 ? true : false,
                                        aid       : Item.aid,
                                   });
                             }) ;
                        
                             this.list.forEach(Item=>{
                                  this.Aid.push(Item.aid)
                             });
                             
                         }
                    }
                })
                .catch(err=>{
                     
                })

            }else{
                this.$toast("请先登录！");
                setTimeout(()=>{
                      this.$router.push({name:"Login"})
                },500)
                
            }
        },
         methods: {
              goBack(){
                this.$router.go(-1);
              },
              onAdd() {
                  this.isPath = true;
                  this.$router.push({name:'Addlocat',params:{'isPath':this.isPath,Length:this.Length,Aid:this.Aid}})
                 
              },
              onEdit(item) {
                   this.isPath = false;
                   this.$router.push({name:'Addlocat',params:{'isPath':this.isPath,aid:item.aid,Aid:this.Aid}});
              },
            
             
         }
    }
</script>

<style lang="less" scoped>
      .myLocation{
          height: 100%;
          background-color: #f5f5f5;

        /deep/.van-address-list{
            background: #f5f5f5;
            height: 0px;
            padding-bottom: 0px;
        }
        /deep/.van-nav-bar{
            background: transparent;
        }
        /deep/.van-icon{
            font-size: 16px;
            font-weight: 500;
            color: #000;
        }

        /deep/.van-address-list__bottom{
            background-color: #f5f5f5;
            

            /deep/.van-button--danger{
                width: 95%;
                margin: 0px auto;
                border: none;
                background-color: #6d87c4;
                position: fixed;
                bottom: 40px;
            }
        }

        /deep/.van-address-item .van-radio__icon--checked .van-icon{
          display: none;

        }
        /deep/.van-tag--danger{
            background-color: #6d87c4;
            font-size: 12px;
            position: relative;
            top: -1px;
        }
        /deep/.van-address-item__name{
            font-size: 14px;
        }
        /deep/.van-icon-success{
            display: none;
        }
      }
</style>